
.StorageScrollList {
  height: calc(100% - 38px);
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.StorageScrollList .mint-loadmore, .StorageScrollList .mint-loadmore-content, .StorageScrollList .wrap {
    height: 100%;
}
.StorageScrollList .wrap {
    padding: 0.13333rem 0;
    position: relative;
}
.StorageScrollList .wrap .mint-spinner-double-bounce {
      margin: 5px auto;
}



