
.LaunchMeeting {
  background: #fff;
  height: 100%;
}
.LaunchMeeting * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.LaunchMeeting .mint-tabbar {
    top: 45px;
    height: 0.93333rem;
}
.LaunchMeeting .mint-tabbar .mint-tab-item {
      height: 0.93333rem;
      color: #999999;
      padding: 0.26667rem 0;
      background: transparent;
      position: relative;
}
.LaunchMeeting .mint-tabbar .mint-tab-item:after {
        content: '';
        position: absolute;
        bottom: 0;
        height: 0.05333rem;
        width: 0;
        left: 50%;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
        background: #cb1e2e;
        -webkit-transition: width .3s ease-in-out;
        transition: width .3s ease-in-out;
}
.LaunchMeeting .mint-tabbar .mint-tab-item.is-selected {
        color: #666666;
}
.LaunchMeeting .mint-tabbar .mint-tab-item.is-selected:after {
          width: 100%;
}
.LaunchMeeting .mint-tabbar .mint-tab-item .mint-tab-item-label {
        font-size: 0.4rem;
}
.LaunchMeeting .mint-tab-container {
    top: 0.93333rem;
}
.LaunchMeeting .picker-item {
    padding: 0 0.13333rem;
}



